.f-input--underlined .f-input__field {
  color: inherit;
  background-color: white;
  border-bottom: 1px solid silver;
  transition: border-color 0.2s cubic-bezier(0.51, 0.92, 0.24, 1);
  will-change: border-color;
  padding: 1em 0; }
  .f-input--underlined .f-input__field:hover {
    border-color: #9c27b0; }
  .f-input--underlined .f-input__field:focus {
    border-color: #9c27b0; }
  .f-input--underlined .f-input__field[disabled] {
    cursor: not-allowed;
    border-color: gainsboro; }
  .f-input--underlined .f-input__field:-webkit-autofill:focus, .f-input--underlined .f-input__field:-webkit-autofill:active {
    -webkit-box-shadow: inset 0 0 0 10em white !important; }

.f-input--underlined.has-error .f-input__field,
.f-input--underlined.has-error .f-input__field:hover,
.f-input--underlined.has-error .f-input__field:focus {
  border-color: #cc0000; }

.f-input--underlined .f-input__label {
  position: absolute;
  display: flex;
